li,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  font-size: normal;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input {
  border: 0;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Bold.woff") format("woff");
  font-weight: 700;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Light.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Light.woff") format("woff");
  font-weight: 300;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Regular.woff") format("woff");
  font-weight: 400;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Medium.woff") format("woff");
  font-weight: 500;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto/Roboto-Italic.woff2") format("woff2"), url("../fonts/Roboto/Roboto-Italic.woff") format("woff");
  font-weight: 400;
  font-style: italic;
}

@font-face {
  font-family: 'Bernier Distressed';
  src: url("../fonts/Bernier/bernierdistressed-regular-webfont.woff2") format("woff2"), url("../fonts/Bernier/bernierdistressed-regular-webfont.woff") format("woff");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: 'Bernier';
  src: url("../fonts/Bernier/bernierregular-regular-webfont.woff2") format("woff2"), url("../fonts/Bernier/bernierregular-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Bernier Shade';
  src: url("../fonts/Bernier/berniershade-regular-webfont.woff2") format("woff2"), url("../fonts/Bernier/berniershade-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'FontAwesome';
  src: url("../fonts/FontAwesome/fontawesome.woff2") format("woff2"), url("../fonts/FontAwesome/fontawesome.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  font-family: 'Roboto';
  font-size: 16px;
}

.arrow {
  height: 20px;
  width: 25px;
  display: block;
}

.arrow__icon {
  fill: rgba(255, 255, 255, 0.7);
}

.arrow__icon:hover {
  transition: .3s;
  fill: #fff;
}

.button {
  cursor: pointer;
  display: block;
  width: 100%;
  height: 100%;
  color: #fff;
  font-size: 16px;
  padding: 15px 25px;
  background-color: #0ab19e;
}

.button_works,
.button_submit {
  border-radius: 0 0 0 4px;
}

.button_blog,
.button_reset {
  border-radius: 0 0 4px 0;
}

.button:hover {
  transition: .3s;
  background-color: rgba(0, 150, 136, 0.7);
}

.button_submit,
.button_reset {
  background-color: #009688;
}

.button_submit:hover,
.button_reset:hover {
  background-color: #0ab19e;
}

.buttons {
  display: flex;
  border-radius: 0 0 4px 4px;
}

.buttons__item {
  flex: auto;
}

.buttons__item:not(:last-of-type) {
  border-right: 2px solid #73cac2;
}

.credits {
  color: rgba(255, 255, 255, 0.7);
}

.credits__author:after {
  content: '|';
  display: inline-block;
  margin: 0 4px;
}

.footer {
  background-color: rgba(55, 62, 66, 0.6);
  color: rgba(255, 255, 255, 0.7);
}

.footer__row {
  display: flex;
  justify-content: space-between;
  padding: 15px 30px;
}

.footer__row:not(:last-of-type) {
  border-bottom: 1px solid #6b7879;
}

.footer__col {
  width: 40%;
  display: flex;
}

.footer__col_right {
  justify-content: flex-end;
}

.footer__menu-text,
.footer__credits {
  height: fit-content;
  margin: auto 0;
}

.menu-text {
  display: flex;
  color: #fff;
}

.menu-text__item:not(:last-of-type) {
  margin-right: 40px;
}

.hero {
  background-image: url("../images/bg.jpg");
  background-size: cover;
  min-height: 620px;
  position: relative;
}

.hero_blog {
  min-height: 400px;
}

.hero__intro {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
}

.hero__arrow {
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -100%);
  z-index: 10;
}

.hero__triangles {
  position: absolute;
  top: 100%;
  left: 0;
  transform: translateY(-100%);
}

.header {
  display: flex;
}

.header__socials {
  margin-right: auto;
  margin-top: 3%;
  margin-left: 3%;
}

.menu {
  display: block;
  width: 36px;
  height: 36px;
  position: relative;
  margin-top: 3%;
  margin-right: 3%;
}

.menu__icon {
  height: 5px;
  width: 100%;
  background-color: #fff;
  border-radius: 2px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.menu__icon:before {
  content: '';
  display: block;
  width: 36px;
  height: 5px;
  position: absolute;
  bottom: 300%;
  background-color: #fff;
  border-radius: 2px;
}

.menu__icon:after {
  content: '';
  display: block;
  width: 36px;
  height: 5px;
  position: absolute;
  top: 300%;
  background-color: #fff;
  border-radius: 2px;
}

.triangles {
  stroke: #fff;
}

.triangles__left {
  fill: #efeee9;
}

.triangles__right {
  fill: #eeebdf;
}

.triangles_works .triangles__left,
.triangles_works .triangles__right {
  fill: #faf8f0;
}

.triangles_blog .triangles__left,
.triangles_blog .triangles__right {
  fill: #fff;
}

.intro {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.intro__avatar {
  border-radius: 50%;
  z-index: 20;
  margin-bottom: 10px;
  max-width: 150px;
}

.intro__text {
  display: flex;
  flex-direction: column;
  z-index: 20;
}

.intro__text_blog {
  color: #fff;
}

.intro__name {
  font-size: 35px;
  font-weight: 500;
}

.intro__name_welcome {
  color: #455a64;
}

.intro__name_hero {
  color: white;
}

.intro__site {
  font-weight: 300;
}

.intro__site_welcome {
  color: rgba(69, 90, 100, 0.85);
}

.intro__site_hero {
  color: rgba(255, 255, 255, 0.85);
}

.intro__svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: scale(4) translate(-12.5%, -12.5%);
  z-index: 5;
  fill: rgba(255, 255, 255, 0.15);
}

.social {
  display: block;
  height: 100%;
  width: 100%;
}

.social__icon {
  position: relative;
}

.social__icon_welcome {
  fill: #0ab19e;
}

.social__icon_welcome:hover {
  transition: .3s;
  fill: rgba(0, 150, 136, 0.7);
}

.social__icon_hero,
.social__icon_footer {
  fill: rgba(255, 255, 255, 0.7);
}

.social__icon_hero:hover,
.social__icon_footer:hover {
  transition: .3s;
  fill: #fff;
}

.social__icon_footer {
  width: 36px;
  height: 36px;
}

.socials {
  display: flex;
  justify-content: center;
  align-items: center;
}

.socials__item:not(:last-of-type) {
  margin-right: 30px;
}

.socials__item_footer:not(:last-of-type) {
  margin-right: 10px;
}

.subtitle {
  display: flex;
  justify-content: center;
}

.subtitle__text {
  text-transform: uppercase;
  font-family: 'Bernier';
  width: fit-content;
  color: #455a64;
  font-size: 35px;
  position: relative;
  margin: 35px 0;
}

.subtitle__text:before {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-150%, -50%);
}

.subtitle__text:after {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(50%, -50%);
}

.subtitle__text:hover:after,
.subtitle__text:hover:before {
  width: 25px;
  height: 5px;
  transition: width .5s, height .5s;
}

.subtitle__text_contacts {
  margin: 30px 0;
}

.subtitle__text_form {
  margin: 10px 0 20px 0;
}

.title {
  display: flex;
  justify-content: center;
  position: relative;
}

.title__text {
  text-transform: uppercase;
  font-family: 'Bernier';
  width: fit-content;
  color: #455a64;
  font-size: 63px;
  position: relative;
  margin: 70px 0;
  z-index: 20;
}

.title__text:before {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-200%, -50%);
}

.title__text:after {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(100%, -50%);
}

.title__text:hover:after,
.title__text:hover:before {
  width: 25px;
  height: 5px;
  transition: width .5s, height .5s;
}

.title__text_avatar-area {
  margin: 0;
}

.title__text_intro {
  color: #fff;
  margin: 0;
}

.title__text_talk {
  width: 60%;
  text-align: center;
  line-height: 1.05;
}

.title__svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.title__svg_works {
  fill: #f0eee7;
  transform: scale(1.5) translate(-33.33%, -33.33%);
}

.title__svg_intro {
  fill: rgba(255, 255, 255, 0.15);
  transform: scale(1.5) translate(-33.33%, -33.33%);
}

.title__svg_avatar-area {
  fill: #e6e5e0;
}

.title__svg_talk {
  transform: scale(1.5) translate(-33%, -33%);
  fill: #ebecec;
}

.personal {
  min-height: 835px;
  display: flex;
}

.personal__left-col {
  width: 50%;
  padding: 0 8% 4% 8%;
  background-color: #efeee9;
}

.personal__right-col {
  width: 50%;
  padding: 0 2% 4% 2%;
  background-color: #eeebdf;
}

.personal__skills {
  margin-top: 65px;
}

.avatar-area__title {
  margin-bottom: 70px;
}

.avatar-area__img {
  max-width: 100%;
}

.text-area {
  color: #455a64;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.text-area h1 {
  width: fit-content;
  max-width: 60%;
  text-transform: uppercase;
  font-family: 'Bernier';
  font-size: 35px;
  text-align: center;
  margin-bottom: 40px;
  margin-top: 40px;
  position: relative;
}

.text-area h1:before {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-150%, -50%);
}

.text-area h1:after {
  content: '';
  display: block;
  width: 25px;
  height: 5px;
  width: 0;
  height: 0;
  transition: width .5s, height .5s;
  background-color: rgba(202, 203, 200, 0.65);
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(50%, -50%);
}

.text-area h1:hover:after,
.text-area h1:hover:before {
  width: 25px;
  height: 5px;
  transition: width .5s, height .5s;
}

.text-area_left h1:first-of-type {
  margin-top: 0;
}

.skills__title {
  font-size: 28px;
  font-weight: 400;
  color: #455a64;
}

.skills__title::first-letter {
  text-transform: uppercase;
}

.skills__list {
  display: flex;
}

.skills__item:hover .circle__sector {
  stroke-dashoffset: 125.6;
  transition: stroke-dashoffset 1s;
}

.skills__item:not(:last-of-type) {
  margin-bottom: 35px;
}

.circle {
  position: relative;
  width: 120px;
  height: 120px;
}

.circle__icon {
  fill: none;
  stroke-width: 20;
}

.circle__main {
  stroke: #ccc;
  fill: none;
  position: relative;
}

.circle__sector {
  stroke: #0ab19e;
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
  stroke-dasharray: 251.2;
  stroke-dashoffset: 251.2;
  transition: stroke-dashoffset 1s;
}

.circle__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #0ab19e;
  max-width: 70px;
  word-wrap: break-word;
  text-align: center;
  font-size: 12px;
  line-height: 14.7px;
}

.map {
  background-image: url("../images/bg.jpg");
  background-size: cover;
  min-height: 980px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.map__contacts {
  width: 380px;
  height: fit-content;
}

.map__footer {
  position: absolute;
  top: 100%;
  width: 100%;
  transform: translateY(-100%);
}

.contacts {
  background-color: rgba(255, 255, 255, 0.7);
  padding: 0 40px 30px 40px 30px;
  color: #455a64;
}

.contacts__icon {
  fill: #0ab19e;
}

.contacts__item {
  display: flex;
}

.contacts__item:not(:last-of-type) {
  margin-bottom: 20px;
}

.contacts__icon {
  margin-right: 10px;
}

.blog {
  display: flex;
  padding-top: 70px;
}

.blog__paper-list {
  padding-left: 40px;
  width: 25%;
}

.blog__papers {
  width: 75%;
  padding-left: 90px;
}

.paper-list {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  text-align: right;
}

.paper-list__item:not(:last-of-type) {
  margin-bottom: 25px;
}

.paper-name {
  color: #455a64;
  width: fit-content;
  transition: border-right .3s;
  border-right: 3px solid transparent;
}

.paper-name__link {
  display: block;
  width: 100%;
  height: 100%;
  padding-right: 20px;
}

.paper-name:hover {
  color: #0ab19e;
  border-right: 3px solid #0ab19e;
  transition: border-right .5s;
}

.papers {
  padding-right: 110px;
}

.papers__item {
  padding-bottom: 70px;
}

.papers__item:not(:last-of-type) {
  border-bottom: 5px solid #f1f1f2;
}

.papers__item:not(:first-of-type) {
  padding-top: 70px;
}

.paper__title {
  font-family: 'Bernier';
  font-size: 40px;
  color: #455a64;
  margin-bottom: 30px;
}

.paper__date {
  color: #0ab19e;
  margin-bottom: 30px;
}

.main {
  background-image: url("../images/bg.jpg");
  background-size: cover;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.main__auth {
  align-self: flex-end;
  margin: 20px;
}

.main__welcome {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 380px;
}

.main__credits {
  margin-bottom: 3%;
}

.welcome {
  text-align: center;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 4px;
  height: 100%;
}

.welcome__socials {
  margin-top: 25px;
}

.welcome__info {
  display: flex;
  flex-direction: column;
  padding: 35px 60px;
}

.welcome__avatar {
  border-radius: 50%;
  max-width: 40%;
  max-width: 150px;
}

.welcome__text {
  display: flex;
  flex-direction: column;
  color: #455a64;
}

.welcome__name {
  font-size: 28px;
}

.btn-auth {
  display: block;
  height: 100%;
  padding: 15px 25px;
  border-radius: 4px;
  border: 1px solid white;
  color: white;
}

.btn-auth:hover {
  border: 1px solid transparent;
  transition: background-color .3s, color .3s;
  background-color: white;
  color: #0ab19e;
  font-weight: 500;
}

.works__title {
  background-color: #faf8f0;
  background-image: url("images/");
}

.slider {
  display: flex;
  height: 100vh;
  flex-direction: column;
  flex-wrap: wrap;
}

.slider__descr {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #efeee9;
}

.slider__preview {
  height: 50%;
  width: 50%;
  background-color: #eeebdf;
}

.slider__controls {
  height: 50%;
  width: 50%;
}

.descr {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.descr__tech {
  text-transform: uppercase;
  padding: 20px 0;
  margin-bottom: 35px;
  text-align: center;
  border-bottom: 1px solid #0ab19e;
  border-top: 1px solid #0ab19e;
  color: #0ab19e;
  width: 120%;
  margin-bottom: 35px;
}

.btn-show {
  display: block;
  width: 100%;
  height: 100%;
  padding: 15px 20px;
  display: flex;
  background-color: #0ab19e;
  color: #fff;
  border-radius: 5px;
}

.btn-show__icon {
  fill: #fff;
  margin-right: 10px;
}

.btn-show:hover {
  background-color: #009688;
}

.controls {
  display: flex;
}

.controls__item {
  position: relative;
  width: 50%;
  border-right: 1px solid rgba(255, 255, 255, 0.7);
  background-color: rgba(199, 198, 193, 0.6);
}

.controls__item:hover {
  background-color: rgba(188, 186, 177, 0.6);
}

.controls__link {
  display: block;
  width: 100%;
  height: 100%;
}

.controls__icon {
  fill: rgba(255, 255, 255, 0.7);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.controls__icon:hover {
  fill: #fff;
}

.controls__icon_next {
  transform: rotate(180deg);
}

.talk {
  background-image: url("../images/bg-works.jpg");
  background-size: cover;
  min-height: 1240px;
  position: relative;
}

.talk__top-area {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 60px;
}

.talk__bottom-area {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.talk__arrow {
  margin-bottom: 8%;
  transform: rotate(180deg);
}

.talk__form {
  margin-bottom: 8%;
}

.container {
  margin: 0 15%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.quotes {
  display: flex;
  flex-wrap: wrap;
  color: #455a64;
}

.quotes__item {
  width: 45%;
}

.quotes__item:not(:nth-of-type(2n)) {
  margin-right: 5%;
}

.quotes__symb {
  font-family: 'FontAwesome';
  color: #d5d7d8;
  margin-bottom: 35px;
}

.quotes__text {
  margin-bottom: 25px;
}

.person {
  display: flex;
  color: #0ab19e;
}

.person__avatar {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  margin-right: 20px;
  overflow: hidden;
  position: relative;
}

.person__img {
  max-width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form {
  width: 380px;
  border-radius: 4px;
  background-color: rgba(252, 252, 252, 0.7);
}

.form__input-area {
  margin: 5%;
}

.form__input,
.form__text-area {
  padding: 20px;
  font-size: 16px;
  outline: none;
  border-radius: 5px;
  border: 0;
  width: 100%;
}

.form__text-area {
  min-height: 140px;
  resize: none;
}

.form__input {
  margin-bottom: 15px;
}

@media (max-width: 1200px) {
  html {
    font-size: 16px;
  }
}

@media (max-width: 992px) {
  html {
    font-size: 15px;
  }
}

@media (max-width: 760px) {
  html {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 13px;
  }

  .credits__author {
    display: block;
  }

  .credits__author:after {
    display: none;
  }

  .credits {
    text-align: center;
  }

  .main__auth {
    margin: auto;
  }

  .main__welcome {
    width: 90%;
    height: 65%;
    position: static;
    transform: translate(0, 0);
    margin: auto;
  }

  .main__credits {
    margin: auto;
    width: 70%;
  }

  .welcome__info {
    height: 100%;
    justify-content: center;
  }

  .welcome__avatar {
    max-width: 70%;
  }
}